.about {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100ch;
  flex-direction: column;
  gap: 30px;
}
.about h1 {
  font-family: Spaces;
  font-size: 40px;
  width: 50%;
  text-align: center;
  color: #fff;
}
.about button {
  background: none;
  border: none;
  color: #fff;
  text-decoration: underline;
  font-size: 16px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  cursor: pointer;
  transition: 0.2s;
}
.about button:hover {
  color: #5127cf;
}

.moreAboutCont {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7411764706);
  z-index: 200;
  animation: aboutAnim 0.7s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.moreAboutCont button {
  position: absolute;
  right: 20px;
  top: 20px;
  font-size: 30px;
  background: none;
  border: none;
  color: #fff;
  font-family: Spaces;
  font-weight: bold;
  cursor: pointer;
}
.moreAboutCont p {
  color: #fff;
  width: 60%;
  font-size: 30px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
  animation: moreTextAnima 2s;
}

@keyframes moreTextAnima {
  0% {
    transform: translateY(40px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes aboutAnim {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media (max-width: 1100px) {
  .about h1 {
    width: 90%;
    font-size: 30px;
  }
  .moreAboutCont p {
    width: 90%;
    font-size: 20px;
  }
}
@media (max-width: 500px) {
  .about h1 {
    width: 100%;
    font-size: 25px;
  }
  .moreAboutCont p {
    width: 99%;
    font-size: 17px;
  }
}/*# sourceMappingURL=About.module.css.map */