header{
  width: 100%;
  height: 120ch;
}
.headerText{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.textCont{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  h1{
    color: #fff;
    font-size: 100px;
    font-family: Spaces;
    font-weight: bolder;
    text-shadow: 0 0 15px #5127cf;
  }
  p{
    font-size: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #5127cf;
  }
}

.scrollText{
  position: absolute;
  color: #fff;
  bottom: 0;
  right: 50px;
  transform: translateX(-50%);
  padding: 0 10px;
  font-size: 20px;
  animation: scrollText 2s infinite;
  p{
    rotate: 90deg;
    font-size: 50px;
    color: #5127cf;
    pointer-events: none;
  }
  @keyframes scrollText {
    0%{
      bottom: 20px;
    }
    50%{
      bottom: 0;
    }
    100%{
      bottom: 20px;
    }    
  }
}
.loadedText{
  animation: loadedAnim 2s;
}

@media (max-width: 1100px) {
  header{
    height: 100ch;
  }
  .textCont{
    h1{
      font-size: 70px;
    }
  }
}
@media (max-width: 700px) {
  .textCont{
    h1{
      font-size: 50px;
    }
  }
  .scrollText{
    display: none;
  }
}
@media (max-width: 500px) {
  .textCont{
    h1{
      font-size: 33px;
      font-weight: bold;
    }
  }
}